<template>
  <div class="wrapper">
    <el-table :data="tableData" stripe>
      <el-table-column v-if="index" type="index"></el-table-column>
      <el-table-column
        v-for="col in columns"
        :ref="col.id"
        :prop="col.id"
        :key="col.id"
        :label="col.label"
      >
        <template slot-scope="scope">
          <slot :name="col.id" :scope="scope">{{scope.row[col.id]}}</slot>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <slot name="btnArea" :scope="scope">
            <el-button type="primary" size="small" @click="editItem(scope.row)">编辑</el-button>
            <el-button type="danger" size="small" @click="deleteItem(scope.row.id)">删除</el-button>
          </slot>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "normalTable",
  props: {
    tableData: Array,
    columns: Array,
    flag: String,
    changed: Object,
    created: Object,
    index: Boolean,
    deleteFlag: Boolean
  },
  watch: {
    changed: function(stu) {
      if (stu !== null) {
        for (let i of this.tableData) {
          if (i.id === stu.id) {
            const index = this.tableData.indexOf(i);
            this.tableData.splice(index, 1, stu);
            break;
          }
        }
      }
    },
    created: function(student) {
      if (student !== null) {
        this.tableData.unshift(student);
      }
    }
  },
  methods: {
    addNew(item) {
      if (this.flag === "add") {
        this.tableData.unshift(item);
      }
    },
    editItem(row) {
      this.$emit("handleEdit", row);
    },
    deleteItem(id) {
      this.$confirm("是否删除", "提示", {
        comfirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$emit("handleDelete", id);
          if (this.deleteFlag == true) {
            for (let i of this.tableData) {
              if (i.id == id) {
                const index = this.tableData.indexOf(i);
                this.tableData.splice(index, 1);
                break;
              }
            }
          }
        })
        .catch(() => {
          return;
        });
    }
  }
};
</script>

